.main {
    background-color: #1f1f20;
    display: flex;
    justify-content: center;
    width: 100%;
    padding-top: 100px;

}

.container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 1200px;
    flex-wrap: wrap;
    margin-bottom: 20px;
    margin-top: 20px;

}

.greeting {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 20px;
    color: white;
    font-weight: bold;

}

.greeting span:nth-child(1) {
    font-size: 16px;
}

.greeting span:nth-child(2) {
    font-weight: 700;
    font-stretch: normal;
    font-size: 33px;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 5px;
}
h1{
    font-size: 26px;
}

.photo {
    background-color: darkkhaki;
    width: 300px;
    height: 400px;
}

.photo img {
    width: 100%;
    height: 100%;
}

@media (max-width: 480px) {
    .photo {
        width: 250px;
        height: 350px;
    }

}


